body,
html {
    padding: 0;
    margin: 0;
}

body {
    height: 100%;
    width: 100%;

    background: url("/src/images/santa.jpg") no-repeat center center fixed;
    background-size: cover;
}

.App {
    margin: 30px 20% 30px 20%;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

@media (max-width: 640px){
    body{
        background: url("/src/images/santa_640px.jpg") no-repeat center center fixed;
    }
}