.inputField {
    display: flex;
    justify-content: center;
}
.participants_list__header{
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 5px;
}
.participiants_list {
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 10px;
    padding: 20px;
    margin: 30px 0;
}
.participiant_single {
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 15px;
    padding: 20px;
    margin: 30px 0;
    display: flex;
    justify-content: space-between;
}

.startButton{
    width: 100px;
    align-self: center;
}

@media (max-width: 640px) {
    .participiants_list {
        justify-content: center;
    }
}